<template>
    <main>
      <video class="fullscreen" src="../../assets/background-movie.mp4" id="movie-background" playsinline autoplay muted loop>
      </video>
      <div class="form-wrapper">
        <img src="../../assets/logo-white.png" id="logo" />
        <h1>Faça seu login</h1>
        <form v-on:submit="Signin">
          <div class="form-item">
            <label for="email"></label>
            <input v-model="email" type="email" name="email" required="required" placeholder="E-mail" />
          </div>
          <div class="form-item">
            <label for="password"></label>
            <input v-model="password" type="password" name="password" required="required" placeholder="Senha" />
          </div>
          <div class="button-panel">
            <input type="submit" class="button" title="Entrar" value="Entrar" />
          </div>
        </form>
        <div class="form-footer">
          <p><a href="#">Esqueci minha senha</a></p>
        </div>
      </div>
    </main>
</template>

<script>
import api from '../../services/api.js'

import './styles.css'

export default {
    name: 'Signin',
    data() {
      return {
        email: '',
        password: ''
      }
    },
    /* mounted() {
      api.get('/usuarios').then(response => {
        console.log(response.data)
      })
    }, */
    methods: {
      async Signin(event) {
        event.preventDefault();
        const email = this.email
        const password = this.password
        
        const response = await api.post('/usuarios/logon', { email, password })

        if(response.data) {
          alert("OK")
        } else {
          alert("Login inválido")
        }
      }
    }
}
</script>